ul{
    list-style-type: none;
    padding: 0;
}

.menu ul {
    background-color: #222;
    margin: 0;
}

.menu li {
    font-family: sans-serif;
    font-size: 1.2em;
    line-height: 40px;
    height: 40px;
    border-bottom: 1px solid #888;
}

.menu a {
    text-decoration: none;
    color: #fff;
    display: block;
}
.App{
    width: 50%;
    margin: auto;
    justify-content: center;
    align-items: center;
    text-align: center;
}